$( document ).ready(function() {
    // menu

    let menuElements = $('.header-main__menu, .header-main__elements');

    menuElements.on('click', function() {
        $('.header-main__elements, .dropdown').toggleClass('active');
    })

    //animation 

    let elements = document.querySelectorAll('.dropdown__element');

    console.log(elements);

    let increment = 0.05;

    elements.forEach(element => {
        increment += 0.05;
        element.style.transitionDelay = `${increment}s`;
    })
});